import { Vue } from 'vue-property-decorator';
import { StorageUser } from '@/../helpers';
import { IStorage } from '@/../models';

export class ServiceUserClass {
    constructor() {
        Vue.prototype.$user = {
            sessionId: '',
            user: {
                name: '',
            },
        };
    }

    public initVueUser(): void {
        let tempUser: IStorage.IUser = StorageUser.getStorage();
        if (tempUser.sessionId != '') {
            Vue.prototype.$user = tempUser;
        }
    }
}

export const ServiceUser = new ServiceUserClass();
