import { IComponent } from '@/../models';
import { EFormType, EDatetimeFormat } from '@/../enums';

export class ServiceFormConfigClass {
    private formConfig: IComponent.IFormConfig = {
        id: '',
        cols: 12,

        label: '',
        placeholder: '',

        formType: EFormType.text,

        // is required
        required: false,
        requiredErrorTip: '',

        // regex
        regex: null,
        regexErrorTip: '',

        // number
        max: null,
        min: null,
        step: 1,
        decimalLength: 0,

        // file
        accept: '',
        multiple: false,

        // date
        dateFormat: EDatetimeFormat.date_dash_YYYYMMDD,

        // switch
        height: 40,
        width: 108,
        labelsChecked: '',
        labelsUnchecked: '',
        colorChecked: '#82C7EB',
        colorUnchecked: '#BFCBD9',
        colorDisabled: '#CCCCCC',
        margin: 3,
        fontSize: 12,

        // image
        src: '',
        orignalSrc: '',
        defaultSrc: '',
    };

    public getFormConfig(): object {
        return this.formConfig;
    }

    private formRelationSelectConfig: IComponent.IFormRelationSelectConfig = {
        id: '',
        label: '',
        placeholder: '',
        formType: EFormType.single,
        required: false,
        cols: 12,
    };

    public getFormRelationSelectConfig(): object {
        return this.formRelationSelectConfig;
    }
}

export const ServiceFormConfig = new ServiceFormConfigClass();
