setting$.pipe(RxOperator.takeUntil(stop$)).subscribe({
    next: (x) => {
        title = x.tab.title;
        showPageName = x.showPageName === true ?? false;

        if (document.title) {
            const _subTitle: string = document.title.split('-')[1];
            window.document.title = `${title}${_subTitle ? '-' + _subTitle : ''}`;
        }

        changeMetaTitle(routes);
    },
});

/**
 * Change meta title recursively
 * @param routes
 */
function changeMetaTitle(routes) {
    for (const route of routes) {
        if (!!route && Array.isArray(route.children) && route.children.length > 0) {
            changeMetaTitle(route.children);
        } else {
            if (!route.meta) {
                route.meta = {
                    title: title,
                };
            } else if (route.meta?.title) {
                const subTitle: string = route.meta.title.split('-')[1];
                route.meta.title = `${title}${subTitle ? '-' + subTitle : ''}`;
            } else {
                window.document.title = title;
            }
        }
    }
}

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
});

/// navigation guards
router.beforeEach(async (to, from, next) => {
    let user = StorageUser.getStorage();

    // check auth
    if (to.meta?.auth !== false && to.name !== 'Login') {
        if (!user || user?.sessionId == '') {
            next({ name: 'Login' });
        }
    }

    // check permission
    let isPermission: boolean = true;
    if (Array.isArray(to.meta?.permission) && to.meta.permission.length > 0) {
        isPermission = to.meta.permission.some((item) => user.user?.roles.findIndex((role) => role.name === item) > -1);
    }

    let vue = new Vue();

    if (isPermission) {
        if (showPageName) {
            console.log(to.name);
        }
        next();
    } else {
        let result = await ServiceDialog.ErrorConfirmOnly(vue, vue.$i18n.Error_Response_401, vue.$i18n.Status_Error);

        if (!from.name) {
            // direct type url in url bar
            next({ name: 'Home' });
        } else {
            next(false);
        }
    }
});

router.afterEach((to, from) => {
    window.document.title = to.meta.title;
});

export default router;
