import { Vue } from 'vue-property-decorator';
import { StorageLanguage } from '@/../helpers';
import { IStorage } from '@/../models';

import { I18n, II18n, I18nOptions } from '@/assets/i18n';

declare module 'vue/types/vue' {
    export interface Vue {
        $i18n: IStorage.Ti18n<typeof I18n>;
    }
}
let LanguageI18n: II18n.ILanguage = {
    'en-US': 'English',
    'i18n-key': 'i18n-key',
};

export class ServiceI18nClass {
    private defaultLanguage: string = II18n.ELanguage['en-US'];
    private browserLanguage: string = navigator.language;

    /**
     * name
     */
    public get languageI18n(): II18n.ILanguage {
        return {
            ...LanguageI18n,
            ...I18nOptions,
        };
    }

    public setDefaultLanguage(): void {
        let storageLanguage = StorageLanguage.getStorage();
        if (!storageLanguage) {
            // save language
            StorageLanguage.saveStorage(this.browserLanguage ? this.browserLanguage : this.defaultLanguage);
        }
        this.setVueI18n();
    }

    public setVueI18n(): any {
        let self = this;
        let storageLanguage = StorageLanguage.getStorage();
        let originalI18n = JSON.parse(JSON.stringify(I18n));

        let typeI18n: any = {};
        Object.keys(I18n).map(function(objectKey, index) {
            typeI18n[objectKey] = '';
        });
        let i18n: IStorage.Ti18n<typeof I18n> = typeI18n;

        // get language options
        Object.keys(originalI18n).map(function(objectKey, index) {
            i18n[objectKey] = '';
            if (originalI18n[objectKey]) {
                if (originalI18n[objectKey][storageLanguage] === undefined && storageLanguage === II18n.ELanguage['i18n-key']) {
                    i18n[objectKey] = objectKey;
                } else if (originalI18n[objectKey][storageLanguage]) {
                    i18n[objectKey] = originalI18n[objectKey][storageLanguage];
                } else if (originalI18n[objectKey][self.defaultLanguage]) {
                    i18n[objectKey] = originalI18n[objectKey][self.defaultLanguage];
                } else {
                    i18n[objectKey] = `!@!${objectKey}!@!`;
                }
            } else {
                i18n[objectKey] = `!!!${objectKey}!!!`;
            }
        });

        Vue.prototype.$i18n = i18n;
    }

    setI18nInterface() {}
}

export const ServiceI18n = new ServiceI18nClass();
