import { IStorage } from '@/../models';

export class StorageUserClass {
    private storageKey: string = 'user';

    public saveStorage(data: IStorage.IUser): void {
        localStorage.setItem(this.storageKey, JSON.stringify(data));
    }

    public clearStorage(): void {
        localStorage.removeItem(this.storageKey);
    }

    public getStorage(): IStorage.IUser {
        let result: IStorage.IUser = {
            sessionId: '',
            user: {},
        };
        try {
            let localInfo = localStorage.getItem(this.storageKey);
            if (localInfo == undefined) {
                return result;
            }
            return JSON.parse(String(localInfo)) as IStorage.IUser;
        } catch (e) {
            console.error(`${this.storageKey} error: `, e);
        }
        return result;
    }
}

export const StorageUser = new StorageUserClass();
