export class StorageLanguageClass {
    private storageKey: string = 'Language';

    public saveStorage(data: string): void {
        localStorage.setItem(this.storageKey, JSON.stringify(data));
    }

    public clearStorage(): void {
        localStorage.removeItem(this.storageKey);
    }

    public getStorage(): string {
        let result: string = '';
        try {
            let localInfo = localStorage.getItem(this.storageKey);

            if (localInfo == undefined) {
                return result;
            }
            let tempData = JSON.parse(String(localInfo)) as string;
            if (tempData != undefined) {
                result = tempData;
            }
        } catch (e) {
            console.error(`${this.storageKey} error: `, e);
        }
        return result;
    }
}

export const StorageLanguage = new StorageLanguageClass();
