    /**
     * @param  {any} vue
     * @param  {string} content
     * @param  {string} title?
     * @param  {string} confirmButtonText?
     * @param  {string} cancelButtonText?
     * @param  {string} iconTitle?
     * @param  {number} timer?
     * @returns Promise
     */
    export function ErrorDeitalConfirnOnly(
        serverDetailErrorMessage: string,
        vue: any,
        content: string,
        title?: string,
        confirmButtonText?: string,
        cancelButtonText?: string,
        iconTitle?: string,
        timer?: number,
    ): Promise<SweetAlertResult> {
        return Swal.fire({
            title: `${title ? title : vue.$i18n.Common_Status_Error}`,
            html: `
            <i class="fas fa-times-circle mt-4" style='color: red; font-size: 6rem;'></i>
            <h5 class='pt-2 font-weight-bold'>${iconTitle ? iconTitle : vue.$i18n.Common_Status_Error}</h3>
            
            <div class='p-2'> ${content} </div>

            <div class='col-md-12 mb-2'>
                <button href="javascript:void(0)"
                style='
                    background-color: rgb(186,200,230); border-radius: 50%; font-size: 10px;
                    border: none; outline: none; padding: 1px 4px; float: right;'
                onclick=' {
                    let detailDiv = document.getElementById("detailDiv"),
                        displayValue = "";
                    if (detailDiv.style.display === "") displayValue = "none";
                    detailDiv.style.display = displayValue;
                    }'
                >
                    <i class="fas fa-angle-down" style='color: #fff';></i>
                </button>
                <br />
            </div>

            <div id="detailDiv"
                style="display:none; font-size: 10px;max-height: 100px; overflow-y: auto;
                margin-left: 8px;"
            >
                ${serverDetailErrorMessage}
            </div>
            `,
            allowOutsideClick: false,
            showConfirmButton: true,
            confirmButtonColor: '#537895',
            confirmButtonText: `${confirmButtonText ? confirmButtonText : vue.$i18n.Button_Confirm}`,
            showCancelButton: false,
            cancelButtonColor: '#FFFFFF',
            cancelButtonText: `${cancelButtonText ? cancelButtonText : vue.$i18n.Button_Cancel}`,
            timer: timer === undefined ? undefined : timer,
            customClass: {
                container: 'container-class',
                popup: 'popup-class',
                header: 'header-class',
                title: 'title-class',
                closeButton: 'close-button-class',
                content: 'content-class',
                confirmButton: 'confirm-button-class',
                cancelButton: 'cancel-button-class',
            },
        });
    }

