import { Vue } from 'vue-property-decorator';
import Toasted from 'vue-toasted';
Vue.use(Toasted);

export class ServiceToastClass {
    public successDefaultTitle: string = 'Success';
    public errorDefaultTitle: string = 'Error';

    public success(message: string = '', duration: number = 2000) {
        let self: any = Vue;
        let tempMessage: string = message ? message : this.successDefaultTitle;
        let successMessage: string = `<i class="fas fa-check"></i> &nbsp;  ${tempMessage}`;
        let toast = self.toasted.show(successMessage, {
            theme: 'toasted-primary',
            position: 'bottom-center',
            duration,
            className: 'method-success',
        });
    }

    public error(message: string = '', duration: number = 2000) {
        let self: any = Vue;
        let tempMessage: string = message ? message : this.errorDefaultTitle;
        let errorMessage: string = `<i class="fas fa-times"></i> &nbsp;  ${tempMessage}`;
        let toast = self.toasted.show(errorMessage, {
            theme: 'toasted-primary',
            position: 'bottom-center',
            duration,
            className: 'method-error',
        });
    }
}
export const ServiceToast = new ServiceToastClass();
