import { IStorage } from '@/../models';

export class StorageRemeberClass {
    private storageKey: string = 'Remember';

    public saveStorage(data: IStorage.IRemember): void {
        localStorage.setItem(this.storageKey, JSON.stringify(data));
    }

    public clearStorage(): void {
        localStorage.removeItem(this.storageKey);
    }

    public getStorage(): IStorage.IRemember {
        let result: IStorage.IRemember = {
            username: '',
            password: '',
        };
        try {
            let localInfo = localStorage.getItem(this.storageKey);
            if (localInfo == undefined) {
                return result;
            }
            let tempData: IStorage.IRemember = JSON.parse(String(localInfo));
            if (tempData.username != undefined) {
                result.username = tempData.username;
            }
            if (tempData.password != undefined) {
                result.password = tempData.password;
            }
        } catch (e) {
            console.error(`${this.storageKey} error: `, e);
        }
        return result;
    }
}

export const StorageRemeber = new StorageRemeberClass();
