import { ServiceDialog, ServiceTypeof, ServiceResolveText } from '@/../helpers';
import { IService } from '@/../models';
import { WebPagePath } from '@/config';

export namespace ServiceResponseFilter {
    export interface IErrorObj {
        statusCode?: number;
        message: string;
        detail?: string;
    }

    /**
     * @param  {any} vue
     * @param  {any} response
     * @param  {Function} callback?
     * @param  {string=''} errorMessage
     * @param  {boolean=true} check
     * @returns void
     */
    export function successCheck(vue: any, response: any, callback?: Function, errorMessage: string = '', check: boolean = true): void | boolean {
        let responseItem: IService.IResponseFilter = {
            totalLength: 0,
            errorLength: 0,
            errorMessageList: [],
        };

        let responseData: any = response.data;

        const CONTENT_RESPONSEFAIL: string = vue.$i18n.Error_Response_Fail;
        const TITLE_ERROR: string = vue.$i18n.Common_Status_Error;

        // response undefined
        if (!response) {
            ServiceDialog.ErrorConfirmOnly(vue, CONTENT_RESPONSEFAIL, TITLE_ERROR);
            return false;
        }

        if (!responseData) {
            ServiceDialog.ErrorConfirmOnly(vue, CONTENT_RESPONSEFAIL, TITLE_ERROR);
            return false;
        }

        // if control for page
        if (!check) {
            callback(response);
            return false;
        }

        // check response data for multiple response
        for (let data of responseData) {
            responseItem.totalLength++;

            if (data.statusCode === undefined) {
                continue;
            }

            if (ServiceTypeof.isNumber(data.statusCode) && data.statusCode !== 200) {
                responseItem.errorLength++;
                if (data.message && ServiceTypeof.isNumber(data.message)) {
                    if (errorMessage) {
                        responseItem.errorMessageList.push(errorMessage);
                    } else {
                        responseItem.errorMessageList.push(data.message);
                    }
                }
            }
        }

        if (responseItem.errorLength > 0) {
            let message: string = '';
            for (let errorMessage of responseItem.errorMessageList) {
                message += `${errorMessage}<br>`;
            }
            ServiceDialog.ErrorConfirmOnly(vue, message, TITLE_ERROR);
        } else {
            callback(response);
            return false;
        }
    }

    /**
     *
     *
     * @export
     * @param {*} vue
     * @param {*} e
     * @param {string} [message='']
     * @return {*}  {boolean}
     */
    export function catchError(vue: any, e: any, message: string = ''): boolean {
        const TITLE_ERROR: string = vue.$i18n.Common_Status_Error;

        let errorObj: IErrorObj = handleError(vue, e, message);
        let detail: string = errorObj.detail;

        if (!!detail && detail.includes('<')) detail = detail.replace('<', ServiceResolveText.left_lt);
        if (!!detail && detail.includes('>')) detail = detail.replace('>', ServiceResolveText.right_gt);

        ServiceDialog.ErrorDeitalConfirnOnly(detail, vue, errorObj.message, TITLE_ERROR);

        if (errorObj.statusCode === 401 || errorObj.statusCode === 403) {
            if (vue.$route.path === WebPagePath.Login) return false;
            vue.$router.push(WebPagePath.Login);
        }
    }

    /**
     *
     *
     * @export
     * @param {*} vue
     * @param {*} e
     * @param {string} [message='']
     * @return {*}  {IErrorObj}
     */
    export function handleError(vue: any, e: any, message: string = ''): IErrorObj {
        const CONTENT_RESPONSEFAIL: string = vue.$i18n.Error_Response_Fail;
        const CONTENT_400: string = vue.$i18n.Server_Exception_400;
        const CONTENT_401: string = vue.$i18n.Server_Exception_401;
        const CONTENT_403: string = vue.$i18n.Server_Exception_403;
        const CONTENT_500: string = vue.$i18n.Server_Exception_500;

        if (e.response) {
            console.error('error response: ', e.response);

            let errorResponse: any = e.response;
            let errorResponseData: string = e?.response?.data ?? '';
            let errorResponseStatus: number = e?.response?.status ?? undefined;
            let errorMessage: string = '';

            if (!errorResponse) {
                return {
                    message: CONTENT_RESPONSEFAIL,
                };
            }

            if (!errorResponseStatus) {
                return {
                    message: CONTENT_RESPONSEFAIL,
                };
            }

            if (message) {
                return {
                    statusCode: errorResponseStatus,
                    message: message,
                    detail: errorResponseData,
                };
            }

            if (errorResponseData === 'Failed to fetch') {
                return {
                    message: CONTENT_RESPONSEFAIL,
                };
            }

            if (errorResponseStatus === 400) {
                return {
                    statusCode: 400,
                    message: CONTENT_400,
                    detail: errorResponseData,
                };
            }

            if (errorResponseStatus === 401) {
                return {
                    statusCode: 401,
                    message: CONTENT_401,
                    detail: errorResponseData,
                };
            }

            if (errorResponseStatus === 403) {
                return {
                    statusCode: 403,
                    message: CONTENT_403,
                    detail: errorResponseData,
                };
            }

            if (errorResponseStatus === 500) {
                return {
                    statusCode: 500,
                    message: CONTENT_500,
                    detail: errorResponseData,
                };
            }

            if (errorResponseStatus === 503) {
                return {
                    statusCode: 503,
                    message: errorMessage,
                    detail: errorResponseData,
                };
            }

            return {
                statusCode: errorResponseStatus,
                message: CONTENT_500,
                detail: errorResponseData,
            };
        }

        return {
            message: CONTENT_RESPONSEFAIL,
        };
    }
}
