import { ServiceDatetime, ServiceDatetimeClass, ServiceBase64, ServiceRegex, ServiceServerConfig } from '@/../helpers';
import { IComponent } from '@/../models';
import { EDatetimeFormat } from '@/../enums';
import { encode } from 'html-entities';

export namespace ServiceResolveText {
    /**
     * @param  {string|Date=undefined} dateValue
     * @param  {string|EDatetimeFormat=EDatetimeFormat.date_slash_YYYYMMDD} format
     * @returns string
     */
    export function resolveDate(
        dateValue: string | number | Date = undefined,
        format: string | EDatetimeFormat = EDatetimeFormat.date_slash_YYYYMMDD,
    ): string {
        if (!dateValue) return undefined;

        let result: string = '';
        let date: Date = undefined;

        if (typeof dateValue === 'string' || typeof dateValue === 'number') {
            date = new Date(dateValue);
        } else if (dateValue instanceof Date) {
            date = dateValue;
        }

        if (date instanceof Date && !isNaN(date.getTime())) {
            result = `${ServiceDatetime.datetime2String(date, format)}`;
        }

        return result;
    }

    /**
     * @param  {boolean} value
     * @param  {any} vue
     * @param  {string=vue.$i18n.Common_Status_Yes} trueValue
     * @param  {string=vue.$i18n.Common_Status_No} falseValue
     * @param  {string} bothValue?
     * @returns string
     */
    export function resolveBooleanToString(
        value: boolean,
        vue: any,
        trueValue: string = vue.$i18n.Common_Status_Yes,
        falseValue: string = vue.$i18n.Common_Status_No,
        bothValue?: string,
    ): string {
        if ((value === undefined || value === null) && !bothValue) return '';
        if ((value === undefined || value === null) && bothValue) return bothValue;
        if (value === true) return trueValue;
        if (value === false) return falseValue;
    }

    /**
     * @param  {IComponent.ISelectionBase} value
     * @returns string
     */
    export function resolveObjectName(value: IComponent.ISelectionBase): string {
        if (!value) return '';
        return value.name ?? '';
    }

    /**
     * @param  {string} value
     * @returns string
     */
    export function resolvePhotoIsUrlOrBase64(value: string): string {
        if (!value) return undefined;
        if (ServiceBase64.isBase64Image(value)) return value;

        const imageURL: string = ServiceServerConfig.getURL + value;
        return ServiceRegex.isUrl(imageURL) ? imageURL : '';
    }

    /**
     * @param  {string} value
     * @returns string
     */
    export function resolveCard(value: string): string {
        if (!value) return '';
        return value.padStart(8, '0');
    }

    /**
     * @param  {any[]} value
     * @param  {string} variableName
     * @returns string
     */
    export function resolveArrayToString(value: any[], variableName: string): string {
        let result: string = '';

        (value || []).forEach((item) => {
            if (item[variableName]) result += ` ${item[variableName]}\r\n`;
        });

        return result;
    }

    /**
     * @param  {any[]} value
     * @param  {string} variableName
     * @param  {string} joinValue
     * @returns string
     */
    export function resolveArrayJoinToString(value: any[], variableName: string, joinValue: string): string {
        return (value || []).map((item: any) => item[variableName]).join(joinValue);
    }

    /**
     * @param  {number} score
     * @returns string
     */
    export function resolveScore(score: number): string {
        if (score === undefined || score === null) return '';
        return ((Math.round(score * 10000) * 100) / 10000).toString() + '%';
    }

    /**
     *
     *
     * @export
     * @param {string} inTime
     * @param {string} outTime
     * @param {*} vue
     * @return {*}  {string}
     */
    export function resolveStayTime(inTime: string, outTime: string, vue: any): string {
        if (!inTime || !outTime) return '';

        let result: string = '';
        const dateObject: ServiceDatetimeClass.IDisassembleDate = ServiceDatetime.disassembleDateDiff(new Date(inTime), new Date(outTime));
        result += result !== '' || dateObject.hour > 0 ? `${dateObject.hour.toString().padStart(2, '0')}:` : '00:';
        result += result !== '' || dateObject.minute > 0 ? `${dateObject.minute.toString().padStart(2, '0')}:` : '00:';
        result += result !== '' || dateObject.second > 0 ? `${dateObject.second.toString().padStart(2, '0')}` : '00';
        if (!result) result = vue.$i18n.Date_MinuteLess1;
        return result;
    }

    /**
     * decide how many words to display in ui
     *
     * @export
     * @param {string} string
     * @param {number} [length=30]
     * @return {*}  {string}
     */
    export function resolveStringLength(string: string, length: number = 30): string {
        if (!string) return '';
        return string.length <= 30 ? string : string.slice(0, 29) + '...';
    }

    /**
     * encode special characters to normal string
     *
     * @export
     * @param {string} string
     * @return {*}  {string}
     */
    export function encodeString(string: string): string {
        return encode(string);
    }

    export const left_lt: string = encodeString('<');
    export const right_gt: string = encodeString('>');
}
