import { StorageUser } from '@/../helpers';
import { IService } from '@/../models';
import { DevelopServerConfig } from '@/config';

export class ServiceServerConfigClass {
    private PRODUCTION: string = 'production';
    private HTTP: string = 'http:';
    private HTTPS: string = 'https:';

    private serverInfo: IService.IServerConfigInfo = {
        protocol: location.protocol,
        host: window.location.hostname,
        port: window.location.port,
        hostUrl: '',
    };

    constructor() {
        this.initServerInfo();
    }

    private initServerInfo(): void {
        let productMode: string = process.env.NODE_ENV;
        let tempPortString: string = '';

        if (productMode === this.PRODUCTION) {
            this.serverInfo.hostUrl += this.serverInfo.protocol;
            this.serverInfo.hostUrl += '//';
            this.serverInfo.hostUrl += this.serverInfo.host;
            this.serverInfo.hostUrl += this.serverInfo.port != '' ? `:${this.serverInfo.port}` : '';
        } else {
            this.serverInfo.host = DevelopServerConfig.Host;
            this.serverInfo.port =
                this.serverInfo.protocol === this.HTTPS ? DevelopServerConfig.HttpsPort.toString() : DevelopServerConfig.HttpPort.toString();
            if (this.serverInfo.protocol === this.HTTP && DevelopServerConfig.HttpPort != 80) {
                tempPortString = ':' + DevelopServerConfig.HttpPort.toString();
            } else if (this.serverInfo.protocol === this.HTTPS && DevelopServerConfig.HttpsPort != 443) {
                tempPortString = ':' + DevelopServerConfig.HttpsPort.toString();
            }
            this.serverInfo.hostUrl += this.serverInfo.protocol;
            this.serverInfo.hostUrl += '//';
            this.serverInfo.hostUrl += this.serverInfo.host;
            this.serverInfo.hostUrl += tempPortString;
        }
    }

    private resolveParamValue(param: IService.IServerConfigParam[], key: string, data: any): void {
        let self = this;
        let tempType = typeof data;
        switch (tempType) {
            case 'undefined':
            case 'function':
                break;
            case 'boolean':
            case 'number':
            case 'string':
                param.push({
                    key: key,
                    value: data,
                });
                break;
            case 'object':
                if (data === null) {
                    param.push({
                        key: key,
                        value: null,
                    });
                } else if (Array.isArray(data)) {
                    for (let subValue of data) {
                        this.resolveParamValue(param, key, subValue);
                    }
                } else {
                    Object.keys(data).map(function(subKey, subIndex) {
                        let subValue = data[subKey];
                        self.resolveParamValue(param, `${key}.${subKey}`, subValue);
                    });
                }
                break;
        }
    }

    public paramUrl(uri: string, data: { [propName: string]: any } = {}): string {
        let self = this;
        let url = this.hostUrl + uri;
        let user = StorageUser.getStorage();
        let param: IService.IServerConfigParam[] = [];
        if (user.sessionId != '') {
            data.sessionId = user.sessionId;
        }
        Object.keys(data).map(function(objectKey, index) {
            self.resolveParamValue(param, objectKey, data[objectKey]);
        });
        for (let index in param) {
            let headerKey = '&';
            let tempItem = param[index];
            if (parseInt(index) == 0) {
                headerKey = '?';
            }
            url += `${headerKey}${tempItem.key}=${tempItem.value}`;
        }
        url = encodeURI(url);
        return url;
    }

    public url(uri: string): string {
        return encodeURI(this.hostUrl + uri);
    }

    public get getURL(): string {
        return `${this.serverInfo.hostUrl}/`;
    }

    get protocol(): string {
        return this.serverInfo.protocol;
    }

    get host(): string {
        return this.serverInfo.host;
    }

    get port(): string {
        return this.serverInfo.port;
    }

    get portString(): string {
        return this.serverInfo.port.toString();
    }

    get hostUrl(): string {
        return this.serverInfo.hostUrl;
    }
}

export const ServiceServerConfig = new ServiceServerConfigClass();
