import { IService } from '@/../models';

export class ServiceServerStatus {
    private storageKey: string = 'serverStatus';

    /**
     * @returns boolean
     */
    public getControl(): boolean {
        let result: boolean = undefined;
        let localInfo: string = '';

        try {
            localInfo = localStorage.getItem(this.storageKey);
            if (!localInfo) return result;
            let tempInfo: IService.IServerStatus = JSON.parse(String(localInfo));
            if (tempInfo.isControlled !== undefined) result = tempInfo.isControlled;
        } catch (e) {
            console.error('Server Status error: ', e);
        }

        return result;
    }

    /**
     * @returns boolean
     */
    public getLoading(): boolean {
        let result: boolean = undefined;
        let localInfo: string = '';

        try {
            localInfo = localStorage.getItem(this.storageKey);
            if (!localInfo) return result;
            let tempInfo: IService.IServerStatus = JSON.parse(String(localInfo));
            if (tempInfo.loadingStatus !== undefined) result = tempInfo.loadingStatus;
        } catch (e) {
            console.error('Server Status error: ', e);
        }

        return result;
    }

    /**
     * @param  {IService.IServerStatus } statusInfo
     * @returns void
     */
    public saveStatus(statusInfo: IService.IServerStatus): void {
        localStorage.setItem(this.storageKey, JSON.stringify(statusInfo));
    }

    /**
     * @returns void
     */
    public clearStatus(): void {
        localStorage.removeItem(this.storageKey);
    }

    /**
     * @returns IService.IServerStatus
     */
    public getStatus(): IService.IServerStatus {
        let result: IService.IServerStatus = {
            isControlled: false,
            serverName: '',
            loadingStatus: false,
        };
        let localInfo: string = '';

        try {
            localInfo = localStorage.getItem(this.storageKey);
            if (!localInfo) return result;
            let tempInfo: IService.IServerStatus = JSON.parse(String(localInfo));
            if (tempInfo.isControlled !== undefined) result.isControlled = tempInfo.isControlled;
            if (tempInfo.serverName) result.serverName = tempInfo.serverName;
            if (tempInfo.loadingStatus !== undefined) result.loadingStatus = tempInfo.loadingStatus;
        } catch (e) {
            console.error('Server Status error: ', e);
        }

        return result;
    }
}

export default new ServiceServerStatus();
