export enum EDatetimeFormat {
    // date slash
    datetime_slash_YYYYMMDDHHmmss = 'YYYY/MM/DD HH:mm:ss',
    date_slash_YYYYMMDD = 'YYYY/MM/DD',
    date_slash_DDMMYYYY = 'DD/MM/YYYY',

    // date dash
    datetime_dash_YYYYMMDDHHmmss = 'YYYY-MM-DD HH:mm:ss',
    date_dash_YYYYMMDD = 'YYYY-MM-DD',
    date_dash_DDMMYYYY = 'DD-MM-YYYY',

    // time
    timeHHmmss = 'HH:mm:ss',
    timeHHmm = 'HH:mm',
    timemmss = 'mm:ss',

    // year
    year = 'YYYY',

    // year month
    year_slash_YYYYMM = 'YYYY/MM',

    // month
    month = 'MM',

    // week
    week = 'ww',
}
