import axios, { AxiosResponse } from 'axios';
import * as Rx from 'rxjs';

import { ISetting, ILogo, ITab, ILogin } from './models';

const settingDefault: ISetting = {
    tab: {
        title: 'Web X',
    },
    logo: {
        filename: 'logo.png',
        title: 'FRS Edge',
    },
    login: {
        title: 'FRS Edge',
    },
    copyright: 'copyright  Advantech Solution 2022',
    showPageName: false,
    showI18nKey: false,
    i18nMode: '',
};

export const setting$ = new Rx.BehaviorSubject<ISetting>(JSON.parse(JSON.stringify(settingDefault)));

(async () => {
    const publicPath: string = __webpack_public_path__ || '/./';
    const url: string = `${window.location.origin}${publicPath}setting.json`;
    const result: void | AxiosResponse<ISetting> = await axios.get(url).catch((e: any) => console.error(e));

    if (!!result) {
        const tab: ITab = result.data?.tab;
        const logo: ILogo = result.data?.logo;
        const login: ILogin = result.data?.login;
        const copyright: string = result.data?.copyright;
        const showPageName: boolean = result.data?.showPageName;
        const showI18nKey: boolean = result.data?.showI18nKey;
        const i18nMode: string = result.data?.i18nMode;

        let setting: ISetting = JSON.parse(JSON.stringify(settingDefault));

        if (logo) {
            const filename: string = logo.filename;
            const title: string = logo.title;

            setting.logo.filename = filename;
            setting.logo.title = title;
            if (!filename && !title) {
                setting.logo = JSON.parse(JSON.stringify(settingDefault.logo));
            }
        } else {
            setting.logo = JSON.parse(JSON.stringify(settingDefault.logo));
        }

        if (login) {
            const title: string = login.title;

            setting.login.title = title;
            if (!title) {
                setting.login = JSON.parse(JSON.stringify(settingDefault.login));
            }
        } else {
            setting.login = JSON.parse(JSON.stringify(settingDefault.login));
        }

        if (tab) {
            const title: string = tab.title;
            setting.tab.title = title;

            if (!title) {
                setting.tab = JSON.parse(JSON.stringify(settingDefault.tab));
            }
        } else {
            setting.tab = JSON.parse(JSON.stringify(settingDefault.tab));
        }

        setting.copyright = copyright ?? 'copyright  Advantech Solution 2021';
        setting.showPageName = showPageName === true ?? false;
        setting.showI18nKey = showI18nKey === true ?? false;
        setting.i18nMode = !!i18nMode ? i18nMode : '';

        setting$.next(setting);
    } else {
        setting$.next(settingDefault);
    }
})();
